import { Component, OnInit } from '@angular/core';
import { DefinitionService } from '../_services/definition.service';
import { BlockUI, NgBlockUI } from 'ng-block-ui';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { ToastrService } from 'ngx-toastr';
import { AddDocumnettypeComponent } from './add-documnettype/add-documnettype.component';

@Component({
  selector: 'app-document-type',
  templateUrl: './document-type.component.html',
  styleUrls: ['./document-type.component.scss']
})
export class DocumentTypeComponent implements OnInit {

 
   @BlockUI('changelog') blockUIChangelog: NgBlockUI;
   constructor(private definitionService:DefinitionService,
      private modal:NgbModal,
        private toastr: ToastrService
   ) { }
 documents:any[];
   ngOnInit() {
     this.GetDocuments();
   }
 
     GetDocuments(){
 
     this.definitionService.getDocuments().subscribe(res=>{
       this.documents = res; 
     });
     }
 
       reloadChangelog() {
     this.blockUIChangelog.start('Loading..');
 
     setTimeout(() => {
       this.blockUIChangelog.stop();
     }, 2500);
   }


     openSaveModal(){
       const modalref=  this.modal.open(AddDocumnettypeComponent, { size: 'lg' });
      
       modalref.result.then((result)=>{
        this.GetDocuments();
       });
       }
   
       openEditModal(data:any){
         const modalref=  this.modal.open(AddDocumnettypeComponent, { size: 'xl' });
         modalref.componentInstance.hammadde=data;
         modalref.result.then((result)=>{
          this.GetDocuments();
         });
         }
   
   
       delete(id){
           this.definitionService.deleteDocuments(id).subscribe(res=>{
             this.GetDocuments();
             this.toastr.success('', 'Kayt silindi');
           });
       }
}
