import { Component, OnInit } from '@angular/core';
import { ReservationService } from '../_services/reservation.service';
import { BlockUI, NgBlockUI } from 'ng-block-ui';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-reservations',
  templateUrl: './reservations.component.html',
  styleUrls: ['./reservations.component.css']
})
export class ReservationsComponent implements OnInit {


  @BlockUI('changelog') blockUIChangelog: NgBlockUI;
    constructor(private definitionService:ReservationService,  
       private modal:NgbModal,
    private toastr: ToastrService) { }
  documents:any[];
    ngOnInit() {
      this.GetDocuments();
    }
  
      GetDocuments(){
  
      this.definitionService.getDocuments().subscribe(res=>{
        console.log(res);
        this.documents = res; 
      });
      }
  
        reloadChangelog() {
      this.blockUIChangelog.start('Loading..');
  
      setTimeout(() => {
        this.blockUIChangelog.stop();
      }, 2500);
    }

}
