import { Component, OnInit } from '@angular/core';
import { BlockUI, NgBlockUI } from 'ng-block-ui';
import { DefinitionService } from '../_services/definition.service';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { ToastrService } from 'ngx-toastr';
import { AddReservationtypeComponent } from './add-reservationtype/add-reservationtype.component';

@Component({
  selector: 'app-reservation-types',
  templateUrl: './reservation-types.component.html',
  styleUrls: ['./reservation-types.component.scss']
})
export class ReservationTypesComponent implements OnInit {

  @BlockUI('changelog') blockUIChangelog: NgBlockUI;
    constructor(private definitionService:DefinitionService,  
       private modal:NgbModal,
    private toastr: ToastrService) { }
  documents:any[];
    ngOnInit() {
      this.GetDocuments();
    }
  
      GetDocuments(){
  
      this.definitionService.getReservationTypes().subscribe(res=>{
        this.documents = res; 
      });
      }
  
        reloadChangelog() {
      this.blockUIChangelog.start('Loading..');
  
      setTimeout(() => {
        this.blockUIChangelog.stop();
      }, 2500);
    }

  openSaveModal(){
    const modalref=  this.modal.open(AddReservationtypeComponent, { size: 'lg' });
   
    modalref.result.then((result)=>{
     this.GetDocuments();
    });
    }

    openEditModal(data:any){
      const modalref=  this.modal.open(AddReservationtypeComponent, { size: 'xl' });
      modalref.componentInstance.hammadde=data;
      modalref.result.then((result)=>{
       this.GetDocuments();
      });
      }


    delete(id){
        this.definitionService.deleteCompounds(id).subscribe(res=>{
          this.GetDocuments();
          this.toastr.success('', 'Kayt silindi');
        });
    }

    gettype(type){
      if(type==1){
        return "Dakika Bazl";
      }else if(type==2){
        return "Km Bazl";
      }else if(type==3){
        return "Sabit cret";
      }else if(type==4){
        return "ehir D";
      }else{
        return "Bilinmiyor";
      }
    }
}
