import { Component, OnInit } from '@angular/core';
import { DefinitionService } from '../_services/definition.service';
import { BlockUI, NgBlockUI } from 'ng-block-ui';
import { AddLicencetypeComponent } from './add-licencetype/add-licencetype.component';
import { ToastrService } from 'ngx-toastr';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'app-licence-type',
  templateUrl: './licence-type.component.html',
  styleUrls: ['./licence-type.component.scss']
})
export class LicenceTypeComponent implements OnInit {

  @BlockUI('changelog') blockUIChangelog: NgBlockUI;
    constructor(private definitionService:DefinitionService,
          private modal:NgbModal,
              private toastr: ToastrService
    ) { }
  documents:any[];
    ngOnInit() {
      this.GetDocuments();
    }
  
      GetDocuments(){
  
      this.definitionService.getLicences().subscribe(res=>{
        this.documents = res; 
      });
      }
  
        reloadChangelog() {
      this.blockUIChangelog.start('Loading..');
  
      setTimeout(() => {
        this.blockUIChangelog.stop();
      }, 2500);
    }



      openSaveModal(){
        const modalref=  this.modal.open(AddLicencetypeComponent, { size: 'lg' });
       
        modalref.result.then((result)=>{
         this.GetDocuments();
        });
        }
    
        openEditModal(data:any){
          const modalref=  this.modal.open(AddLicencetypeComponent, { size: 'xl' });
          modalref.componentInstance.hammadde=data;
          modalref.result.then((result)=>{
           this.GetDocuments();
          });
          }
    
    
        delete(id){
            this.definitionService.deleteLicenceType(id).subscribe(res=>{
              this.GetDocuments();
              this.toastr.success('', 'Kayt silindi');
            });
        }

}
