import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class DefinitionService {

constructor(private http: HttpClient) { }



  getDocuments(){
   
   return this.http.get<any[]>(environment.apiUrl+"definitions/documenttypes");
  } 

  getLicences(){
   
   return this.http.get<any[]>(environment.apiUrl+"definitions/licencetypes");
  } 

    getReservationTypes(){
   
   return this.http.get<any[]>(environment.apiUrl+"definitions/reservationtypes");
  } 

addCompounds(model:any){
return this.http.post(environment.apiUrl+"definitions/reservationtypes",model);
}

editCompounds(model:any,id:any){
  return this.http.put(environment.apiUrl+"definitions/reservationtypes/"+id,model);
  }

deleteCompounds(id:any){
  return this.http.delete(environment.apiUrl+"definitions/reservationtypes/"+id);
  }


  addDocuments(model:any){
return this.http.post(environment.apiUrl+"definitions/documenttypes",model);
}

editDocuments(model:any,id:any){
  return this.http.put(environment.apiUrl+"definitions/documenttypes/"+id,model);
  }

deleteDocuments(id:any){
  return this.http.delete(environment.apiUrl+"definitions/documenttypes/"+id);
  }

  
  addLicenceType(model:any){
return this.http.post(environment.apiUrl+"definitions/licencetypes",model);
}

editLicenceType(model:any,id:any){
  return this.http.put(environment.apiUrl+"definitions/licencetypes/"+id,model);
  }

deleteLicenceType(id:any){
  return this.http.delete(environment.apiUrl+"definitions/licencetypes/"+id);
  }
}
