import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { BlockUI, NgBlockUI } from 'ng-block-ui';
import { DriverService } from 'src/app/_services/driver.service';

@Component({
  selector: 'app-driver-detail',
  templateUrl: './driver-detail.component.html',
  styleUrls: ['./driver-detail.component.scss']
})
export class DriverDetailComponent implements OnInit {
 @BlockUI('changelog') blockUIChangelog: NgBlockUI;
 constructor(private driverService:DriverService,private route:ActivatedRoute){ }
id:any;
driver:any;
  ngOnInit() {
    this.id=this.route.snapshot.paramMap.get('id');
    this.get();
  }


  get(){
    this.driverService.get(this.id).subscribe(res=>{
      console.log(res);
      this.driver=res});
  } 
 reloadChangelog() {
    this.blockUIChangelog.start('Loading..');

    setTimeout(() => {
      this.blockUIChangelog.stop();
    }, 2500);
  }


  accept(){
    this.driverService.acceptDriver(this.id).subscribe(res=>{
      this.get();
    });
  }
}
