import { Component, Input, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { ToastrService } from 'ngx-toastr';
import { DefinitionService } from 'src/app/_services/definition.service';

@Component({
  selector: 'app-add-documnettype',
  templateUrl: './add-documnettype.component.html',
  styleUrls: ['./add-documnettype.component.css']
})
export class AddDocumnettypeComponent implements OnInit {

 
   submitted=false;
    definitionForm:FormGroup;
 
    @Input() hammadde:any;
    constructor(
      private formBuilder:FormBuilder,
      private activeModal:NgbActiveModal,
      private hammaddeService:DefinitionService,
     
      private toastr: ToastrService
      
    ) { }
  
    ngOnInit() {
  
     this.definitionForm = this.formBuilder.group({
       name: ['', Validators.required],
     
 
     });
 
     if(this.hammadde){
  
     this.definitionForm.patchValue({
       name: this.hammadde.name,
     
      
     });
   }
    }
  
  
    close(){
      this.activeModal.close();
    }
  
  
    save(){
      if(!this.definitionForm.valid){
        this.definitionForm.markAllAsTouched();
       
        return;
      }
      this.submitted=true;
  
  
      console.log(this.definitionForm.value);
     
  
      if(this.hammadde){
        //update
        const updatedModel = { ...this.definitionForm.value, id: this.hammadde.id };
        this.hammaddeService.editDocuments(updatedModel,this.hammadde.id).subscribe(res=>{
          this.submitted=false;
          this.toastr.clear();
          this.toastr.success('', 'Kayt gncelleme baarl');
          this.definitionForm.reset();
       
        },err=>{
          this.toastr.error('', 'Kayt gncelleme srasnda hata olutu');
          this.submitted=false;
        });
      }
      else{
        //insert
        this.hammaddeService.addDocuments(this.definitionForm.value).subscribe(res=>{
          this.submitted=false;
          this.toastr.clear();
          this.toastr.success('', 'Kayt baarl');
          this.definitionForm.reset();
        
        },err=>{
          this.toastr.error('', 'Kayt srasnda hata olutu');
          this.submitted=false;
        });
      }
    
    }

}
